import React, { useState, useEffect, useMemo } from 'react'
import { connect } from 'react-redux'

import Box from '@mui/material/Box'
import Grid from '@mui/material/Grid'
import Dialog from '@mui/material/Dialog'
import Button from '@mui/material/Button'
import Divider from '@mui/material/Divider'
import IconButton from '@mui/material/IconButton'
import Typography from '@mui/material/Typography'
import DialogContent from '@mui/material/DialogContent'
import { fireDialogToast } from '../../../helpers/sweetAlert'
// import DialogActions from '@mui/material/DialogActions'
import OrderList from '../../ui/OrderList'

import Cancel from '@mui/icons-material/Cancel'
import { fetchSales, transferSaleArrear } from '../../../slices/salesSlice'
import { fetchPaymentMethods } from '../../../slices/paymentMethodsSlice'

const DialogRepay = ({
  open,
  sales,
  fetchSales,
  transferSaleArrear,
  fetchPaymentMethods,
  handleClose,
}) => {
  const [inputAmount, setInputAmount] = useState(0)
  const [currentOrderId, setCurrentOrderId] = useState('')
  const [isRepaying, setIsRepaying] = useState(false)

  const currentPaidOrder = useMemo(() => {
    return (sales.list && sales.list.find(order => order.id === currentOrderId)) || {}
  }, [sales, currentOrderId])

  const currentArrearInfo = useMemo(() => {
    return (
      currentPaidOrder?.salesPayments && currentPaidOrder.salesPayments.find(payment => (
        payment.SalePointPayment && payment.SalePointPayment.salePointPaymentsTypeId === '6'
      ))
    ) || {}
  }, [currentPaidOrder])

  useEffect(() => {
    fetchPaymentMethods()
  }, [fetchPaymentMethods])

  useEffect(() => {
    fetchSales({
      isReturn: 0,
      isOwePayment: 1,
    })
  }, [fetchSales])

  useEffect(() => {
    if (sales.list && sales.list[0] && !isRepaying) setCurrentOrderId(sales.list[0].id);
  }, [sales, isRepaying])

  const onFillInClick = () => {
    if (currentArrearInfo && currentArrearInfo.value > 0) {
      setInputAmount(currentArrearInfo.value)
    } else {
      setInputAmount(0)
    }
  }

  const onInputAmountChange = (e) => {
    if (Number.isNaN(Number(e.target.value))) return
    if (Number(e.target.value) > currentArrearInfo.value) {
      fireDialogToast(': ')
      return
    }
    setInputAmount(Number(e.target.value))
  }


  const onPaymentClick = e => {
    if (!inputAmount) {
      fireDialogToast(': ')
      return
    }
    setIsRepaying(true)
    setInputAmount(0)
    const paymentId = e.target.dataset.paymentId
    console.log({
      saleId: currentOrderId,
      arrearId: currentArrearInfo.id,
      paymentId: paymentId,
      value: inputAmount,
    })
    transferSaleArrear({
      saleId: currentOrderId,
      arrearId: currentArrearInfo.id,
      paymentId: paymentId,
      value: inputAmount,
    })
  }

  const onItemClick = e => {
    setInputAmount(0)
    setIsRepaying(false)
  }


  const renderPayments = payments => {
    const restPayments = payments.map(payment => {
      const paymentType = payment.SalePointPayment || {}
      return (paymentType && paymentType.salePointPaymentsTypeId === '6')
        ? null
        : (
          <div key={payment.id} className="payment-span">
            <span className="received-method">{paymentType.paymentName} :&nbsp;</span>
            <span className="received-content">{payment.value}</span>
          </div>
        )
    })

    return (
      <Grid container alignItems="center" sx={{
        px: '0.5rem',
        mb: '0.75rem',
        borderRadius: 1,
        bgcolor: 'background.darkPaper',
      }}>
        <Grid item xs="auto" sx={{
          pr: '2rem',
          fontSize: '1.5rem',
          fontWeight: 'bold',
        }}>
          <span>
             :&nbsp;
          </span>
          <span>
            {currentArrearInfo.value || '- - -'}
          </span>
        </Grid>

        <Grid item xs sx={{
          '& > div': {
            pr: '0.75rem',
            display: 'inline-block',
          }
        }}>
          {restPayments}
        </Grid>
      </Grid>
    )
  }

  return (
    <Dialog
      id="dialog-repay"
      aria-labelledby="dialog-repay"
      fullWidth
      maxWidth="md"
      open={open}
      onClose={handleClose}
    >
      <Box
        sx={{
          flex: '0 0 auto',
          margin: 0,
          padding: '0.375em 1.25em 0.25em',
          lineHeight: 1.6,
          letterSpacing: '0.0075rem',
          bgcolor: 'background.default',
          '& h6': {
            color: 'secondary.main',
            fontSize: '1.375rem',
            fontWeight: 'bold',
            letterSpacing: '0.05em',
          }
        }}
      >
        <Typography variant="h6"></Typography>
        {handleClose ? (
          <IconButton aria-label="close" onClick={handleClose} sx={{
            position: 'absolute',
            right: '0.5em',
            top: '0.25em',
            color: '#cb5c5c',
            padding: 0,
          }}>
            <Cancel sx={{ width: '1.25em', height: '1.25em' }} />
          </IconButton>
        ) : null}
      </Box>

      <DialogContent
        sx={{
          padding: '0.25em 1.75em 0',
          backgroundColor: '#fafafa',
          '& .search-result': {
            overflowY: 'auto',
          },
        }}
      >

        <Grid container alignItems="center" justifyContent="center">
          <Grid item container alignItems="center" justifyContent="space-between"
            sx={{
              pt: '1.25rem',
              pb: '1rem',
              flex: '0 0 95%',
            }}
          >

            <Grid
              item
              xs={6}
              display="inline-flex"
              className="received-amount-input"
              sx={{
                '& input': {
                  flex: '0 0 80%',
                  width: '100%',
                  lineHeight: 2,
                  textAlign: 'center',
                  fontSize: '1.25rem',
                  fontWeight: 'bold',
                  letterSpacing: '0.02em',
                  border: '1px solid #aaaaaa',
                  borderRight: 'none',
                  borderTopLeftRadius: '0.25em',
                  borderBottomLeftRadius: '0.25em',
                  color: (theme) => theme.palette.primary.main,
                  outlineColor: (theme) => theme.palette.primary.main,
                  backgroundColor: 'background.paper',
                  '&::placeholder': {
                    color: '#c4c4c4',
                  },
                },
                '& button': {
                  flex: '0 0 20%',
                  textAlign: 'center',
                  fontSize: '1rem',
                  fontWeight: 'bold',
                  letterSpacing: '0.025em',
                  border: (theme) => `1px solid ${theme.palette.primary.main}`,
                  borderLeft: 'none',
                  borderTopLeftRadius: 0,
                  borderBottomLeftRadius: 0,
                  color: 'background.default',
                  backgroundColor: (theme) => theme.palette.primary.main,
                  '&:hover': {
                    backgroundColor: 'primary.light',
                  },
                  '&:disabled': {
                    borderColor: '#aaaaaa',
                  }
                },
              }}
            >
              <input
                value={inputAmount || ''}
                onChange={onInputAmountChange}
                placeholder=""
              />
              <Button
                variant="contained"
                disableElevation
                onClick={onFillInClick}
              >
                
              </Button>
            </Grid>

            <Grid
              item
              xs={6}
              className="main-payment-method"
              display="inline-flex"
              sx={{
                pl: '2rem',
                '& button': {
                  flex: '0 0 47.5%',
                  fontSize: '1.125rem',
                  fontWeight: 'bold',
                  color: 'background.paper',
                  backgroundColor: 'secondary.matte',
                  '&:first-of-type': {
                    mr: '5%',
                  },
                  '&:hover': {
                    backgroundColor: '#606c9a',
                  },
                },
              }}
            >
              <Button variant="contained" data-payment-id="1" onClick={onPaymentClick}>
                
              </Button>
              <Button variant="contained" data-payment-id="4" onClick={onPaymentClick}>
                
              </Button>
            </Grid>

          </Grid>
        </Grid>

        {renderPayments((currentPaidOrder && currentPaidOrder.salesPayments) || [])}

        <Divider sx={{ mb: '1.25rem' }} />


        <OrderList
          sales={sales}
          fetchSales={fetchSales}
          currentOrderId={currentOrderId}
          setCurrentOrderId={setCurrentOrderId}
          currentPaidOrder={currentPaidOrder}
          searchParams={{ isOwePayment: 1 }}
          handleItemClick={onItemClick}
        />

      </DialogContent>
    </Dialog>
  )
}

const mapStateToProps = state => {
  return {
    sales: state.sales,
  }
}

export default connect(
  mapStateToProps,
  {
    fetchSales,
    transferSaleArrear,
    fetchPaymentMethods,
  }
)(DialogRepay)